@use 'base/variables'as v;
@use 'base/mixins'as m;

.productos {
    padding-block: 5rem;

    &__grid {
        @include m.grid(1, 2rem);

        @include m.tablet { 
        @include m.grid(2, 2rem);
        }

        @include m.desktop { 
            @include m.grid(4, 2rem);
            }
    }
}